:root {
  --background-color: #eaeaea;
  --background-contrast: #131313;
  --accent-color: #ffab59;
  --button-text: #050505;
  --button-color: #ffab59;
  --box-shadow: rgba(0, 0, 0, .267);
}

.darkmode {
  --background-color: #131313;
  --background-contrast: #fff;
  --accent-color: #d2fe59;
  --button-text: #050505;
  --button-color: #eca84e;
  --box-shadow: rgba(250, 150, 0, .09);
}

* {
  box-sizing: border-box;
  color: var(--background-contrast);
  font-family: Krub, sans-serif;
  text-decoration: none;
  list-style: none;
  transition: all .3s;
}

html {
  transition: all .25s;
}

#Darkbutton {
  color: var(--background-contrast);
  cursor: pointer;
}

ul {
  padding: 0%;
}

i {
  padding-right: 10px;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: var(--background-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  line-height: 1.5;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.target * {
  color: var(--background-color);
}

.target {
  width: auto;
  min-width: 20%;
  height: auto;
  min-height: 20%;
  box-shadow: 0px 0px 19px 0px var(--box-shadow);
  background-color: var(--background-contrast);
  border-radius: 1.8em;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  padding: 50px;
  display: flex;
}

.back {
  width: 100%;
  height: 10px;
  color: var(--background-contrast);
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.link {
  color: var(--background-color);
  gap: 10px;
  display: flex;
}

iframe {
  border-radius: 1em;
}

